import Vue from "vue";
import Router from "vue-router";
import Login from "@/components/user/login";
import Register from "@/components/user/register";
import Shouye from "@/components/pages/shouye";
import Edit from "@/components/pages/edit";
import My from "@/components/user/user";
import Blog from "@/components/pages/blog";
import Search from "@/components/pages/search";
import Admin from "@/components/admin/admin";
import mUser from "@/components/admin/m_user";
import mBlog from "@/components/admin/m_blog";
import mAnn from "@/components/admin/announcement";
import Message from "@/components/admin/message";
import Wechat from "@/components/pages/wechat";
import Music from "@/components/pages/music";

Vue.use(Router);

export default new Router({
  mode: "history", //去掉url中的#
  routes: [
    {
      path: "/",
      name: "Login",
      component: Login
    },
    {
      path: "/register",
      name: "Register",
      component: Register
    },
    {
      path: "/shouye",
      name: "Shouye",
      component: Shouye
    },
    {
      path: "/edit",
      name: "Edit",
      component: Edit
    },
    {
      path: "/my",
      name: "My",
      component: My
    },
    {
      path: "/blog",
      name: "Blog",
      component: Blog
    },
    {
      path: "/search",
      name: "Search",
      component: Search
    },
    {
      path: "/wechat",
      name: "Wechat",
      component: Wechat
    },
    {
      path: "/music",
      name: "Music",
      component: Music
    },
    {
      path: "/admin",
      name: "Admin",
      component: Admin,
      //嵌套路由
      children: [
        {
          path: "/admin/mUser",
          name: "mUser",
          component: mUser
        },
        {
          path: "/admin/mBlog",
          name: "mBlog",
          component: mBlog
        },
        {
          path: "/admin/mAnn",
          name: "mAnn",
          component: mAnn
        },
        {
          path: "/admin/message",
          name: "Message",
          component: Message
        }
      ]
    }
  ]
});



